var Clock = /** @class */ (function () {
    function Clock(clockElementId, clockIndex, offset) {
        var _this = this;
        this.clockElementId = clockElementId;
        this.clockIndex = clockIndex;
        this.editMode = 0;
        this.currentTime = new Date();
        this.offset = offset;
        this.updateClock();
        setInterval(function () { return _this.updateClock(); }, 1000);
    }
    Clock.prototype.padLeft = function (value, length) {
        var strValue = value.toString();
        while (strValue.length < length) {
            strValue = '0' + strValue;
        }
        return strValue;
    };
    Clock.prototype.updateClock = function () {
        var clockElement = document.getElementById(this.clockElementId);
        if (clockElement) {
            var localTime = new Date(this.currentTime.getTime() + this.offset * 3600000);
            var hours = this.padLeft(localTime.getHours(), 2);
            var minutes = this.padLeft(localTime.getMinutes(), 2);
            var seconds = this.padLeft(localTime.getSeconds(), 2);
            clockElement.textContent = "".concat(hours, ":").concat(minutes, ":").concat(seconds);
        }
    };
    Clock.prototype.toggleMode = function () {
        this.editMode = (this.editMode + 1) % 3;
        var modeButton = document.getElementById("mode-".concat(this.clockIndex + 1));
        if (modeButton) {
            if (this.editMode === 0) {
                modeButton.textContent = 'Mode: View';
            }
            else if (this.editMode === 1) {
                modeButton.textContent = 'Mode: Edit Hours';
            }
            else if (this.editMode === 2) {
                modeButton.textContent = 'Mode: Edit Minutes';
            }
        }
    };
    Clock.prototype.increaseTime = function () {
        if (this.editMode === 1) {
            this.currentTime.setHours(this.currentTime.getHours() + 1);
        }
        else if (this.editMode === 2) {
            this.currentTime.setMinutes(this.currentTime.getMinutes() + 1);
        }
        this.updateClock();
    };
    Clock.prototype.resetTime = function () {
        this.currentTime = new Date();
        this.updateClock();
    };
    return Clock;
}());
export default Clock;
