import './index.css';
import Clock from './watch-time';
import LightManager from './watch-light';
var ClockManager = /** @class */ (function () {
    function ClockManager() {
        this.clocks = [];
        this.lightManager = new LightManager();
        for (var i = 1; i <= 3; i++) {
            var clock = new Clock("clock-time-".concat(i), i - 1, i - 1);
            this.clocks.push(clock);
            this.bindClockButtons(i, clock);
        }
        this.bindLightButton();
    }
    ClockManager.prototype.bindClockButtons = function (clockIndex, clock) {
        var modeButton = document.getElementById("mode-".concat(clockIndex));
        var increaseButton = document.getElementById("increase-".concat(clockIndex));
        var resetButton = document.getElementById("reset-".concat(clockIndex));
        if (modeButton) {
            modeButton.addEventListener('click', function () { return clock.toggleMode(); });
        }
        if (increaseButton) {
            increaseButton.addEventListener('click', function () { return clock.increaseTime(); });
        }
        if (resetButton) {
            resetButton.addEventListener('click', function () { return clock.resetTime(); });
        }
    };
    ClockManager.prototype.bindLightButton = function () {
        var _this = this;
        var lightButton = document.getElementById('light');
        if (lightButton) {
            lightButton.addEventListener('click', function () { return _this.lightManager.toggleLight(); });
        }
    };
    return ClockManager;
}());
document.addEventListener('DOMContentLoaded', function () {
    new ClockManager();
});
